<template>
  <transition name="slide-fade">
    <div class="container">
      <div v-title :data-title="this.$route.name"></div>
      <el-form :model="loginForm" :rules="loginRules" ref="loginForm" class="login-form" :class="{'animated shake': invalid}" :label-width="labelWidth">
        <h2 class="page-header"></h2>
        <el-form-item label="  " prop="email">
          <el-input v-model.string="loginForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=" " prop="passwd">
          <el-input type="password" v-model="loginForm.passwd" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPass">
          <el-input type="password" v-model="loginForm.confirmPass" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="authcode">
          <el-input type="text" v-model="loginForm.authcode" placeholder="" style="float: left; width: 65%; margin-right: 15px;"></el-input>
          <canvas id="canvasCode" width="80px" height="35px" class="canvas-code" @click="drawCode"></canvas>
        </el-form-item>
        <el-form-item prop="agreement" label-width="0">
          <el-checkbox-group v-model="loginForm.agreement">
            <el-checkbox name="agreement" label="" v-model="checked"></el-checkbox>
            <a href="javascript:;" @click="dialogVisible = true"></a>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item class="m-b-5" label-width="0">
          <el-button 
          type="primary" 
          :loading="logging"
          style="width: 100%" 
          @click="submitForm">
             
          </el-button>
        </el-form-item>
        <el-form-item label-width="0" style="margin: 0; text-align: center;">
          <span style="margin: 0 10px">
            
            <router-link to="/login"></router-link>
          </span>
        </el-form-item>
      </el-form>
      <el-dialog title="" :visible.sync="dialogVisible">
        <div class="content" style="height: 350px; overflow-y: auto">
          <h4></h4>
          <p> ""www.yueshijue.com""""</p>
          <h5> 1 </h5>
          <p><strong>1.1</strong> </p>
          <p><strong>1.2</strong></p>
          <p><strong>1.3</strong>18</p>
          <p><strong>1.4</strong></p>
          <h5> 2 </h5>
          <p><strong>2.1</strong></p>
          <p><strong>2.2</strong>12 </p>
          <h5> 3 </h5>
          <p><strong>3.1</strong> </p>
          <p><strong>3.2</strong>/ </p>
          <p><strong>3.3</strong> </p>
          <p><strong>3.4</strong><strong></strong></p>
          <p><strong>3.5</strong><strong></strong></p>
          <p><strong>3.6</strong><strong></strong></p>
          <h5> 4 </h5>
          <p> </p>
          <p><strong>1</strong> </p>
          <p><strong>2</strong></p>
          <p><strong>3</strong> </p>
          <p><strong>4</strong></p>
          <p><strong>5</strong></p>
          <p><strong>6</strong></p>
          <p><strong>7</strong></p>
          <p><strong>8</strong></p>
          <p><strong>9</strong></p>
          <p> </p>
          <p><strong></strong></p>
          <p><strong></strong></p>
          <h5> 5 </h5>
          <p> </p>
          <p> """"</p>
          <h5> 6 </h5>
          <p><strong>6.1</strong><span></span> </p>
          <p><strong>6.2</strong><strong></strong> </p>
          <p> <strong>6.3</strong><strong></strong> </p>
          <h5> 7 </h5>
          <p><strong>7.1</strong> </p>
          <p><strong>7.2</strong></p>
          <p><strong>1</strong> </p>
          <p><strong>2</strong></p>
          <p><strong>3</strong></p>
          <p><strong>4</strong></p>
          <h5> 8 </h5>
          <p><strong>8.1</strong><strong></strong> </p>
          <p><strong>8.2</strong><strong>2011</strong> </p>
          <p> <strong>8.3</strong><strong></strong> </p>
          <p><strong>8.4</strong><strong>,,,</strong> </p>
          <p><strong>8.5</strong><strong>,</strong> </p>
          <p><strong>8.6</strong> </p>
          <h5> 9 </h5>
          <p><strong>,""""</strong> </p>
          <p> <strong>,</strong> </p>
          <p><strong></strong></p>
          <p><strong></strong></p>
          <h5> 10 </h5>  <strong><em></em></strong> 
          <h5> 11 </h5>   
          <h5> 12  </h5>
          <p><strong>12.1</strong></p>
          <p><strong>12.2</strong> </p>
          <p><strong>12.3</strong><span></span></p>
          <p><strong>12.4</strong><span>""</span> </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button 
            type="danger" 
            style="width: 100%" 
            @click="dialogVisible = false">
             
          </el-button>
        </span>
      </el-dialog> 
    </div>
  </transition>
</template>
<script>
import Md5 from '@/assets/js/md5'
import Utils from '@/assets/js/utils'
import { requestRegist, requestLogin, accountFind } from '@/api'
export default {
  data() {
    const validateEmail = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else {
        let params = {
          account: value
        }
        accountFind(params).then(res => {
          if(res.data.code === '0001') {
            if(!res.data.result.finded) {
              callback()
            } else {
              callback(new Error(''))
            }
          } else {
            this.$message.error(res.data.message)
          }
        })
      }
    }
    const validateConfirmPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.loginForm.passwd) {
         callback(new Error(''))
      } else {
        callback()
      }
    }
    const validateCode = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else if (value.toUpperCase() !== this.authCode.toUpperCase()) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      labelWidth: '90px',
      dialogVisible: false,
      invalid: false,
      logging: false,
      checked: true,
      authCode: '',
      loginForm: {
        email: '',
        passwd: '',
        confirmPass: '',
        authcode: '',
        agreement: [],
      },
      loginRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' },
          { validator: validateEmail, trigger: 'blur' },
        ],
        passwd: [
          { required: true, message: '', trigger: 'blur' },
          { min: 8, max: 20, message: ' 8  20 ', trigger: 'blur'}
        ],
        confirmPass: [
          { required: true, validator: validateConfirmPass, trigger: 'blur' }
        ],
        authcode: [
          { required: true, validator: validateCode, trigger: 'blur'}
        ],
        agreement: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  methods: {
    drawCode () {
      this.authCode = Utils.canvasCode('canvasCode')
    },
    submitForm() {
      this.$refs.loginForm.validate((valid) => {
        if (valid && !this.logging) {
          this.logging = true;
          let data = {
            email: this.loginForm.email,
            passwd:  Md5.hex_md5(this.loginForm.passwd),
            passwd2: Md5.hex_md5(this.loginForm.confirmPass),
          }
          console.log(data)
          requestRegist(data).then(res => {
            if (res.data.code === '0001') {
              this.$message.success('')
              this.loginSubmit()
              // this.$router.push({ path: '/login' })
            } else {
              this.$message.error(res.data.message)
              this.drawCode()
            }
            this.logging = false
          })
        } else {
          this.invalid = true
          return;
        } 
      })
    },
    loginSubmit() {
      let data = {
        username: this.loginForm.email,
        password: Md5.hex_md5(this.loginForm.passwd),
      }
      requestLogin(data).then(res => {
        if(res.data.code === '0001') {
          let userId = res.data.result.userInfo.userId
          Utils.setCookie('userId', userId)
          this.$message.success('')
          this.$router.push({ path: '/account/infocenter' })
        } else {
          this.$message.error(res.data.message)
        }
      }).catch(err => {
        console.log(err)
        this.$catchError(err)
      })
    }
  },
  mounted () {
    this.drawCode()
  }
}
</script>
<style scoped lang="scss">
  .container {
    overflow: hidden
  }
	.login-form {
		width: 430px;
		margin: 8% auto 0;
		padding: 15px 30px;
		border-radius: 5px;
		background: #fff;
     .canvas-code {
      float: right;
      cursor: pointer;
      border-radius: 4px;
      background-image: linear-gradient(-45deg, #111, #666, #222, #777);
    }
	}
  .content {
    h4 {
      font-weight: bold;
      margin-bottom: 15px;
    }
    h5 {
      margin: 5px 0;
    }
    p {
      margin: 10px 0;
      line-height: 1.3
    }
  }
  .slide-fade-enter-active {
    transition: all .5s;
  }
  .slide-fade-enter, .slide-fade-leave-active {
    opacity: 0;
    transform: translateY(-30px);
  }
  .animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    z-index: 100
  }

  .animated.infinite {
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite
  }

  .animated.hinge {
    -webkit-animation-duration: 2s;
    animation-duration: 2s
  }
  @-webkit-keyframes shake {
    0%,
    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0)
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      -webkit-transform: translateX(-10px);
      transform: translateX(-10px)
    }
    20%,
    40%,
    60%,
    80% {
      -webkit-transform: translateX(10px);
      transform: translateX(10px)
    }
  }

  @keyframes shake {
    0%,
    100% {
      -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
      transform: translateX(0)
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      -webkit-transform: translateX(-10px);
      -ms-transform: translateX(-10px);
      transform: translateX(-10px)
    }
    20%,
    40%,
    60%,
    80% {
      -webkit-transform: translateX(10px);
      -ms-transform: translateX(10px);
      transform: translateX(10px)
    }
  }

  .shake {
    -webkit-animation-name: shake;
    animation-name: shake
  }
</style>



