<template>
	<section>
		<div v-title :data-title="this.$route.name"></div>
		<el-row class="toolbar">
			<label></label>
			<el-select v-model="moduleId" @change="moduleChange">
				<el-option 
					v-for="item in moduleList" 
					:key="item.moduleId"
					:label="item.name"
					:value="item.moduleId">
				</el-option>
			</el-select>
			<el-button type="primary" class="m-l" @click="handleCreateModule"></el-button>
		</el-row>
		<el-card class="card-primary">
			<div slot="header"></div>
			<el-row :gutter="15">
				<el-col :span="5" class="tree" v-loading="loading">
					<ul id="functionTree" class="ztree"></ul>
				</el-col>
				<el-col :span="19" v-if="functionTree.length === 0">
					<el-row class="button-group">
						<el-button size="small" type="success" @click="handleAdd">
							<i class="fa fa-plus-square"></i>
							
						</el-button>
						<el-button size="small" type="warning" @click="handleEdit">
							<i class="fa fa-edit"></i>
							
						</el-button>
						<el-button size="small" type="danger" @click="handleDelete">
							<i class="el-icon-delete"></i>
							
						</el-button>
					</el-row>
					<h3 class="page-header"></h3>
					<el-table
						border
			      :data="funcData"
			      highlight-current-row
			      v-loading="tableLoading" 
			      style="width: 100%">
			      <el-table-column prop="funcId" label="" width="120"></el-table-column>
			      <el-table-column prop="name" label=""></el-table-column>
			      <el-table-column prop="md5" label=""></el-table-column>
			      <el-table-column prop="funcurl" label=""></el-table-column>
			      <el-table-column prop="updateTime" label="" width="120" :formatter="formatTime"></el-table-column>
			      <el-table-column prop="status" label="" :formatter="formatStatus" width="80"></el-table-column>
			      <el-table-column label="" width="80">
			      	<template scope="scope">
			      		<el-button type="primary" size="small" @click="handleShow(scope.row)"></el-button>
			      	</template>
			      </el-table-column>
			    </el-table>
				</el-col>
			</el-row>
		</el-card>
		<!--  -->
		<el-dialog :visible.sync="moduleFormVisible" :title="moduleFormTitle">
			<el-row>
				<el-col :span="18" :offset="3">
					<el-form :model="moduleForm" :rules="moduleRules" ref="moduleForm" label-width="120px">
						<el-form-item label="" prop="name">
							<el-input v-model="moduleForm.name" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="contextRoot">
							<el-input v-model="moduleForm.contextRoot" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="note">
							<el-input type="textarea" v-model="moduleForm.note" placeholder=""></el-input>
						</el-form-item>
					</el-form>
				</el-col>
			</el-row>
			<div slot="footer">
				<el-button @click="moduleFormVisible = false"></el-button>
				<el-button type="primary" @click="moduleFormSubmit"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog :visible.sync="funcFormVisible" :title="funcFormTitle">
			<el-row>
				<el-col :span="18" :offset="3">
					<el-form :model="funcForm" :rules="rules" ref="funcForm" label-width="120px">
						<el-form-item label="" prop="name">
							<el-input v-model="funcForm.name" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="funcMd5">
							<el-input v-model="funcForm.funcMd5" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="funcUrl">
							<el-input v-model="funcForm.funcUrl" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="funcSeq">
							<el-input v-model="funcForm.funcSeq" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="viewname">
							<el-input v-model="funcForm.viewname" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="funcIco">
							<el-input v-model="funcForm.funcIco" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="">
							<el-select v-model="funcForm.isMenu" style="width: 100%">
								<el-option label="" :value="1"></el-option>
								<el-option label="" :value="0"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="">
							<el-select v-model="funcForm.status" style="width: 100%">
								<el-option label="" :value="1"></el-option>
								<el-option label="" :value="0"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="" prop="funcDesc">
							<el-input type="textarea" :rows="2" v-model="funcForm.funcDesc" placeholder=""></el-input>
						</el-form-item>
					</el-form>
				</el-col>
			</el-row>
			<div slot="footer">
				<el-button @click="funcFormVisible = false"></el-button>
				<el-button type="primary" @click="submitForm"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog :visible.sync="funcListVisible" title="">
			<div slot="footer">
				<el-button @click="funcListVisible = false"></el-button>
				<el-button type="primary" @click="funcListVisible = false"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog :visible.sync="funcDetailVisible" title="">
			<el-row>
				<el-col :span="18" :offset="3">
					<el-form :model="funcDetail" label-width="160px">
						<el-form-item label="">
							<span>{{funcDetail.name}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.md5}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.funcurl}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.seq}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.viewname}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.ico}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.ismunu === 1 ? '' : ''}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.status === 1 ? '' : ''}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{funcDetail.desc}}</span>
						</el-form-item>
						<el-form-item label="">
							<span>{{this.$moment(new Date()).format('YYYY-MM-DD')}}</span>
						</el-form-item>
					</el-form>
				</el-col>
			</el-row>
			<div slot="footer">
				<el-button @click="funcDetailVisible = false"></el-button>
				<el-button type="primary" @click="funcDetailVisible = false"></el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import '@/assets/plugins/zTree/css/zTreeStyle.css'
	import '@/assets/plugins/zTree/js/jquery.min.js'
	import '@/assets/plugins/zTree/js/jquery.ztree.all.min.js'
	import { getModuleFunctionList, createFunction, updateFunction, delFunction, setOrganizeStatus, getModules, createModule, updateModule } from '@/api'
	export default {
		data() {
			return {
				moduleId: 20170906001,
				moduleList: [],
				moduleFormTitle: '',
				moduleFormVisible: false,
				functionTree: [],
        funcData: [],
	      checkedNode: null,
	      funcFormVisible: false,
	      funcListVisible: false,
	      funcDetailVisible: false,
	      funcFormTitle: '',
	      funcForm: {
	      	orgId: '',
	      	name: '',
	      	note: '',
	      	parentId: ''
	      },
	      loading: false,
	      tableLoading: false,
	      currentPage: 1,
	      pageSize: 10,
	      total: 0,
	      rules: {
	      	name: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	funcMd5: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	funcUrl: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	funcDesc: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	funcSeq: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	viewname: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	funcIco: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      },
	      funcDetail: {},
	      moduleForm: {
	      	name: '',
	      	contextRoot: '',
	      	note: '',
	      },
	      moduleRules: {
	      	name: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	contextRoot: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      	note: [
	      		{ required: true, message: '', trigger: 'blur'}
	      	],
	      },
			}
		},
		methods: {
			formatStatus(row) {
				return row.status === 1 ? '' : ''
			},
			formatTime(row) {
				return this.$moment(new Date()).format('YYYY-MM-DD ')
			},
			handleSizeChange(val) {
				this.pageSize = val
			},
			handleCurrentChange(val) {
				this.currentPage = val
			},
			// 
			getModuleList() {
				getModules().then(res => {
					if(res.data.code === '0001') {
						this.moduleList = res.data.result.modules
  				} else {
  					this.$message.error(res.data.message)
  				}
				}).catch(err => {
					console.log(err)
					this.$catchError(err)
				})
			},
			// 
			handleCreateModule() {
				this.moduleFormTitle = ''
				this.moduleFormVisible = true
			},
			// 
			moduleFormSubmit() {
				this.$refs.moduleForm.validate(valid => {
					if(!valid) return;
					let data = Object.assign({}, this.moduleForm)
					createModule(data).then(res => {
						if(res.data.code === '0001') {
							this.$message.success(res.data.message)
							this.getModuleList()
	  				} else {
	  					this.$message.error(res.data.message)
	  				}
					}).catch(err => {
						console.log(err)
						this.$catchError(err)
					})
					this.moduleFormVisible = false
				})
			},
			moduleChange(val) {
				this.moduleId = val
				this.getFuncTree()
			},
			// 
			nodeClick(event, treeId, treeNode) {
				if(this.checkedNode === treeNode) return;
				this.checkedNode = treeNode;
				this.funcData = [];
				this.funcData.push(this.checkedNode)
			},
			// 
			getFuncTree() {
				this.loading = true;
				let data = {
					moduleId: this.moduleId
				}
				getModuleFunctionList(data).then(res => {
					if(res.data.code === '0001') {
						const funcTree = res.data.result.functions;
				    const setting = {
				      view: {
				        selectedMulti: false,
				      },
				      data: {
				        simpleData: {
				          enable: true
				        }
				      },
				      callback: {
				      	onClick: this.nodeClick
				      }
				    }
				    const zNode = [];
				    funcTree.forEach(func => {
				      let iconSkin = '';
				      if(!func.parentId){
				        iconSkin = 'root'
				      }else{
				        iconSkin = 'folder'
				      }
				      let treeObj = {
				        id: func.funcId,
				        funcId: func.funcId,
				        pId: func.parentId,
				        name: func.name,
				        md5: func.funcMd5,
				        funcurl: func.funcUrl,
				        seq: func.funcSeq,
				        viewname: func.viewname,
				        ico: func.funcIco,
				        desc: func.funcDesc,
				        status: func.status,
				        ismenu: func.isMenu,
				        open: true,
				        iconSkin: iconSkin,
				      };
				      zNode.push(treeObj)
				    })
				    $.fn.zTree.init($('#functionTree'), setting, zNode);
				    this.checkedNode = null;
					} else {
						this.$message(res.data.message)
					}
					this.loading = false
				}).catch(err => {
					this.loading = false
					console.log(err)
				})
			},
			//
			getSeletedNode() {
				let treeObj = $.fn.zTree.getZTreeObj("functionTree");
			  this.checkedNode = treeObj.getSelectedNodes()[0];
			},
      // 
      handleAdd() {
      	if(!this.checkedNode) {
      		return this.$notify.warning({title: '', message: ''})
      	}
      	this.funcFormTitle = ''
      	this.funcForm = {
      		funcId: '',
      		name: '',
      		funcMd5: '',
      		funcUrl: '',
      		funcSeq: '',
      		viewname: '',
      		funcIco: '',
      		funcDesc: '',
      		status: 1,
      		isMenu: 1,
      		parentId: this.checkedNode.funcId,
      		moduleId: this.moduleId
      	}
      	this.funcFormVisible = true
      },
      // 
      handleEdit() {
      	if (!this.checkedNode) {
      		return this.$notify.warning({title: '', message: ''})
      	}
      	this.funcForm = {
      		funcId: this.checkedNode.funcId,
      		name: this.checkedNode.name,
      		funcMd5: this.checkedNode.md5,
      		funcUrl: this.checkedNode.funcurl,
      		funcSeq: this.checkedNode.seq,
      		viewname: this.checkedNode.viewname,
      		funcIco: this.checkedNode.ico,
      		funcDesc: this.checkedNode.desc,
      		status: this.checkedNode.status,
      		isMenu: this.checkedNode.ismenu,
      	}
      	this.funcFormTitle = ''
      	this.funcFormVisible = true
      },
      // 
      submitForm() {
      	this.$refs.funcForm.validate(valid => {
      		if(valid) {
      			let data = Object.assign({}, this.funcForm)
      			console.log(data)
      			if(data.funcId) {
      				updateFunction(data).then(res => {
      					if(res.data.code === '0001') {
	      					this.$message.success(res.data.message)
	      					this.getFuncTree()
	      				} else {
	      					this.$message.error(res.data.message)
	      				}
      				}).catch(err => {
	      				console.log(err)
	      			})
      			} else {
      				createFunction(data).then(res => {
	      				// console.log(res)
	      				if(res.data.code === '0001') {
	      					this.$message.success(res.data.message)
	      					this.getFuncTree()
	      				} else {
	      					this.$message.error(res.data.message)
	      				}
	      			}).catch(err => {
	      				console.log(err)
	      			})
      			}
      			this.funcFormVisible = false
      		} else {
      			console.log('err submit')
      		}
      	})
      },
      // 
      handleDelete() {
      	if (!this.checkedNode) {
      		return this.$notify.warning({title: '', message: ''})
      	}
      	if(this.checkedNode.children) {
      		return this.$notify.error({title: '', message: ''})
      	}
      	this.$confirm(` ${this.checkedNode.name} `, '', {type: 'warning'}).then(() => {
      		let data = {
	      		funcId: this.checkedNode.funcId
	      	}
	      	delFunction(data).then(res => {
	      		if(res.data.code === '0001') {
	  					this.$message.success(res.data.message)
	  					this.funcData = []
	  					this.getFuncTree()
	  				} else {
	  					this.$message.error(res.data.message)
	  				}
	      	}).catch(err => {
	      		console.log(err)
	      	})
      	}).catch(err => {
      		console.log(err)
      		this.$message('')
      	})
      },
      // 
      handleShow(row) {
      	this.funcDetail = Object.assign({}, row)
      	this.funcDetailVisible = true
      },
		},
		computed: {
			disabled() {
				return this.checkedNode && this.checkedNode.status === 1 ? true : false
			}
		},
		mounted () {
			this.getModuleList()
			this.getFuncTree()
		}
	}
</script>
<style scoped lang="scss">
	.ztree {
		width: 100%;
		min-height: 400px;
		max-height: 600px;
		overflow-y: auto;
		border: 1px solid #ddd;
		&::-webkit-scrollbar {
      width: 4px;
      border-radius: 5px;
      background: #bbb
    }
	}
	.el-card {
		min-height: 500px
	}
	.el-tree {
		min-height: 400px;
		max-height: 600px;
		overflow-y: auto;
		&::-webkit-scrollbar {
      width: 4px;
      border-radius: 5px;
      background: #bbb
    }
	}
	.button-group {
		padding: 10px 20px;
		margin-bottom: 20px;
		box-shadow: 1px 1px 1px 1px #ddd;
		background: #fff;
	}
</style>


